<template>
  <div class="editor" @dragover="dragover" @dragstart="dragstart" @drop="drop">
        <div id="flowEditor"></div>
        <Edit class="text-editor" ref='edit' v-show="editModel" v-model="text" @blurFunc="updateText" :style="{left:left+'px',top:top+'px','min-width':width+'px','min-height':height+'px'}"></Edit>
        <div> <Combine /> </div>
        <Drawer title="导出设计图" :closable="false" v-model="dowloadDetail.isShow" class="facility-draw">
          <div class="upload-win-content">
              <div style="margin-bottom:10px"><span>请输入项目标题：</span><input placeholder="" type="text"  v-model="dowloadDetail.name"/></div>
              <div style="margin-bottom:10px">
              <span>已用设备：</span>
              <!-- <List style="margin-left:40px">
                <ListItem  v-for="item in summary" v-bind:key="item.name">
                  <span v-html="item.name" :title="item.text"></span>
                  <span style="margin-left:10px" v-html="item.cost + '元'" :title="item.cost"></span>
                  <span style="margin-left:10px" v-html="item.count + '个'" :title="item.count"></span>
                </ListItem>
              </List > -->
              <Table :columns="columns" :data="datas"></Table>
              <div style="float:right;font-size:20px;"><span>总计：</span> <span v-text="summary + '元'"></span></div>
              </div>
          </div>
          <div class="upload-win-footer">
              <button @click="download('png')">保存</button>
          </div>
        </Drawer>
        <!-- <div id="download-canvas" style="display:none"></div> -->
         <div id="download-canvas"></div>
  </div>
</template>

<script>
import flowEditor from '../flow/flowEditor';
import eventBus from '../eventbus';
import Edit from './plugin/edit';
import zrender from 'zrender';
import Combine from './Combine'

import getPlainTxt from '../flow/ued/getPlainTxt';

export default {
  name: 'Editor',
  components:{
    Edit,
    Combine
  },
  data(){
     return{
        left:0,
        top:0,
        width:0,
        height:0,
        text:'',
        plainTxt:'',
        editModel:false,
        icon:[],
        colors:'#194d33',
        show:false,
        edge:null,
        showInfoDraw: false,
        offsetX: 0,
        dowloadDetail: {
          isShow : false,
          name : ''
        },
         columns: [
          {
              title: '设备名',
              key: 'name'
          },
          {
              title: '数量(个)',
              key: 'count'
          },
          {
              title: '花费(元)',
              key: 'cost'
          }
        ],
        datas:[],
        summary : 0,
     }
  },
  mounted(){
     var that=this;
     this.editor=new flowEditor('flowEditor');
     eventBus.$on('leftAlign',()=>{
       this.editor.leftAlign();
     });
     eventBus.$on('rightAlign',()=>{
       this.editor.rightAlign();
     });
     eventBus.$on('horizontalAlign',()=>{
       this.editor.horizontalAlign();
     });
     eventBus.$on('verticalAlign',()=>{
       this.editor.verticalAlign();
     });
      eventBus.$on('createGroup',()=>{
       this.editor.createGroup();
     });
     eventBus.$on('redo',()=>{
       this.editor.redo();
     });
     eventBus.$on('undo',()=>{
       this.editor.undo();
     });
      eventBus.$on('undo',()=>{
       this.editor.undo();
     });
     eventBus.$on('updateOffsetX',(e)=>{
       this.offsetX = e.x;
     });
      eventBus.$on('showInfoDraw',()=>{
       if(this.status=='selectNode'){
            eventBus.$emit('showInfoDrawWithNode',{node:this.node});
        }
     });
      eventBus.$on('rotate',()=>{
        if(this.status=='selectNode'||this.status=='selectNodes'){
            this.editor.rotate('node',this.node);
            //eventBus.$emit('clearSelect');  
        }
     });

     
     eventBus.$on('initFlow',(e)=>{
       try{
          this.editor.clearHistory();
          this.editor.refreshHistory();
          this.editor.init(e.data);
          eventBus.$emit('refreshMap',{editor:this.editor});
       }catch(e){
         window.console.log(e);
         alert('不支持的数据');
       }
     });

     eventBus.$on('copy',()=>{
        if(!this.editStatus){
            if(this.status=='selectNode'||this.status=='selectNodes'){
               this.editor.copy('node',this.editor.getSelectNodes());
            }else if(this.status=='selectGroup'){
               this.editor.copy('group',this.group);
            }
        }
     });


     eventBus.$on('mirror',()=>{
        if(!this.editStatus){
            if(this.status=='selectNode'||this.status=='selectNodes'){
               this.editor.mirror('node',this.node);
               //eventBus.$emit('clearSelect');  
            }
        }
     });

     eventBus.$on('delete',()=>{
        if(this.status=='selectNode'){
           this.editor.execute('deleteNode',{node:this.node});
        }else if(this.status=='selectNodes'){
           this.editor.execute('deleteNodes',{nodes:this.nodes});
        }else if(this.status=='selectGroup'){
           this.editor.execute('deleteGroup',{group:this.group});
        }else if(this.status=='selectEdge'){
           this.editor.execute('deleteEdge',{edge:this.edge});
        }
     });

    eventBus.$on('deleteAll',()=>{
      this.editor.execute('deleteAll',{node:this.node});
    });
    eventBus.$on('saveData',()=>{
      this.exportDesign();
      that.dowloadDetail.isShow = true;
    });    
         
      eventBus.$on('changeNode',(e)=>{
           var selectNodes=e.nodes||this.editor.getSelectNodes();
           this.editor.execute('changeNode',e,selectNodes);
      });

      eventBus.$on('align',(e)=>{
         this.editor[e.align]&&this.editor[e.align]();
      });

      eventBus.$on('changeGroup',(e)=>{
         this.editor.execute('changeGroup',e);
      });

      eventBus.$on('changeEdge',(e)=>{
         this.editor.execute('changeEdge',e);
      });

      eventBus.$on('ungroup',()=>{
        if(!this.editStatus){
           if(this.status=='selectGroup'){
             this.editor.execute('ungroup',{group:this.group});
           }
        }
     });

     eventBus.$on('selectFrame',()=>{
        this.editor.emit('selectFrame');
     });

     eventBus.$on('shadow',(e)=>{
         var shadow=e.shadow;
         if(shadow){
           this.editor.setShadow();
         }else{
           this.editor.cancelShadow();
         }
     });
      

     this.editor.listen('selectNode',(e)=>{
          that.status='selectNode';
          that.node=e.node;
          eventBus.$emit('selectNode',{node:e.node});
          if(e.node.kind === 2 || e.node.kind === 3) {
            that.editor.checkObstaceDistance(e.node);
          }
     });

     this.editor.listen('selectNodes',(e)=>{
            if(e&&e.nodes.length>1){
                that.status='selectNodes';
                that.nodes=e.nodes;
                eventBus.$emit('selectNodes',{nodes:e.nodes});
            }else{
                var selectNodes=this.editor.getSelectNodes();
                if(selectNodes.length==1){
                    that.status='selectNode';
                    that.node=selectNodes[0];
                    eventBus.$emit('selectNode',{node:selectNodes[0]});
                }else if(selectNodes.length>1){
                    that.status='selectNodes';
                    that.nodes=selectNodes;
                    eventBus.$emit('selectNodes',{nodes:selectNodes});
                }
            }
     });

     this.editor.listen('selectGroup',(e)=>{
          that.status='selectGroup';
          that.group=e.group;
          eventBus.$emit('selectGroup',e);
     });

     this.editor.listen('selectEdge',(e)=>{
        that.status='selectEdge';
        this.editor.status='selectEdge';
        that.edge=e.edge;
        eventBus.$emit('selectEdge',e);
     });


     this.editor.listen('undoredo',(e)=>{
       eventBus.$emit('undoredo',e);
       eventBus.$emit('refreshMap',{editor:this.editor});
     });

     this.editor.listen('clearSelectItems',()=>{
        eventBus.$emit('clearSelect');
        this.editor.execute('clearOBAlert', this.node);
     });

     function keepLastIndex(obj) {
        if (window.getSelection) {//ie11 10 9 ff safari
            obj.focus(); //解决ff不获取焦点无法定位问题
            let range = window.getSelection();//创建range
            range.selectAllChildren(obj);//range 选择obj下所有子内容
            range.collapseToEnd();//光标移至最后
        }
        else if (document.selection) {//ie10 9 8 7 6 5
            let range = document.selection.createRange();//创建选择对象
            //var range = document.body.createTextRange();
            range.moveToElementText(obj);//range定位到obj
            range.collapse(false);//光标移至最后
            range.select();
        }
    }

     this.editor.listen('edit',(e)=>{
        that.editStatus=e.status;
        var g=new zrender.Group();
        var box=null,text=e.text,box1=null;
        that.editModel=true;
       
        switch(e.status){
           case 'editNode':
            that.node=e.node;
            box=g.getBoundingRect([this.node]);
            that.left=box.x+this.editor.position[0];
            that.top=box.y+this.editor.position[1];
            that.width=box.width;
            that.height=box.height;
            that.text=that.domText(text);
            text='';
            break;
           case 'editGroup':
            that.editGroup=e.group;
            box=g.getBoundingRect([that.group.groupHead]);
            box1=g.getBoundingRect([that.group]);
            that.left=box1.x+this.editor.position[0];
            that.top=box1.y+this.editor.position[1];
            that.width=box.width;
            that.height=box.height;
           // text=that.group.groupHead.style.text;
            that.text=that.domText(text);
            text='';
            break;
           case 'editEdge':
             that.edge=e.edge;
             var d=e.edge.getData();
            if(d.text){
                box=g.getBoundingRect([e.edge.relateText]);
                that.left=box.x+this.editor.position[0];
                that.top=box.y+this.editor.position[1];
                that.width=box.width;
                that.height=box.height;
                that.text=that.domText(d.text);
             }else{
                var cpx=d.cpx;
                that.left=cpx.x1+this.editor.position[0];
                that.top=cpx.y1+this.editor.position[1];
                that.width=80;
                that.height=20;
             }
            break;
           
        }

        that.$nextTick(function () {
           //that.$refs['edit'].$el.focus();
           keepLastIndex( that.$refs['edit'].$el);
        });
     });
  },
  methods:{
     dragover(e){
       e.preventDefault();
     },
     dragstart(e){
       e.preventDefault();
     },
     drop(e){
        if(document.getElementById('combine').style.display != 'none') {
          return;
        }
        var text= e.dataTransfer.getData("addShape");
        var x,y;
        if(e.offsetX) {
          x=parseInt(e.offsetX)+0.5;
          y=parseInt(e.offsetY)+0.5;
        } else {
          x=parseInt(e.pageX)+0.5 - 140;
          y=parseInt(e.pageY)+0.5 - 50;
        }

        try{
          var data=JSON.parse(text);
          data['x']=x;
          data['y']=y;
          this.editor.addShape(data);
        }catch(err){
           //window.console.log(err);
        }
     },
     trim(x) {
        return x.replace(/^\s+|\s+$/gm,'');
     },

     exportDesign() {
       this.dowloadDetail.isShow = true;
       var res = []
       var facilities = this.editor.facilities;
       var summary = 0;
       for(let i=0; i<facilities.length; i++) {
         let has = false;
         for(let j=0; j<res.length; j++) {
           if(facilities[i].originData.name === res[j].name) {
             res[j].count++;
             res[j].cost += facilities[i].originData.cost;
             summary += facilities[i].originData.cost;
             has = true;
           }
         }
         if(!has) {
          res.push({
            name : facilities[i].originData.name,
            count : 1,
            cost : facilities[i].originData.cost
          })
          summary += facilities[i].originData.cost;
         }
       }
       this.datas = res;
       this.summary = summary;
     },

    removeDowloadMsg() {
      var downLoadNodes = this.editor.downLoadText;
      if(downLoadNodes && downLoadNodes.length>0) {
        this.editor.execute('deleteNodes',{nodes:downLoadNodes});
      }
    },

    AddText (config) {
      var textCmd = {
        style: {
            text:  config.name,
            fontSize: config.size,
            fontWeight: config.isBold ? 'bold' : 'normal'
        },
        command:'text',
        position:[0,0],
        draggable: false,
        x:config.x,
        y:config.y,
        kind:6
      }
      this.editor.execute('addShape', textCmd);
    },
    buildDownloadMsg (){
      this.removeDowloadMsg();
      var startY = 30;
      //设计图
      this.AddText({
          name : '设计图:' ,
          size : 20,
          isBold : false,
          x : this.offsetX + 5,
          y : 10
      })
      //标题
      this.AddText({
        name : this.dowloadDetail.name,
        size : 40,
        isBold : true,
        x : this.offsetX + 900,
        y : startY
      })
      startY += 40;
      //设备使用信息
      for(let i=0; i<this.datas.length; i++) {
        startY += 30;
        this.AddText({
          name : this.datas[i].name ,
          size : 20,
          isBold : false,
          x : this.offsetX + 900,
          y : startY
        })
        this.AddText({
          name : this.datas[i].count + '个',
          size : 20,
          isBold : false,
          x : this.offsetX + 1100,
          y : startY
        })
        this.AddText({
          name : this.datas[i].cost + '元',
          size : 20,
          isBold : false,
          x : this.offsetX + 1250,
          y : startY
        })
      }
      startY += 40;
      //总计使用信息
        this.AddText({
          name : "总计： " + this.summary + "元",
          size : 30,
          isBold : true,
          x : this.offsetX + 900,
          y : startY
        })
    },
     download(type){
       if(this.dowloadDetail.name === '') return;
         var box=null;
         switch(type){
           case 'json':
             var str=JSON.stringify(this.editor._());
             this.downloadFile(str,'flowchart.json');
             break;
           case 'png':
                this.buildDownloadMsg();
                //box=this.editor.getBoundingRect(this.editor.nodes.concat(this.editor.groups).concat(this.editor.edges));
                box = {
                  x:this.offsetX,
                  y:this.offsetY,
                  width:1600,
                  height:800
                }

                document.getElementById('flowEditor').style.width=box.width+'px';
                document.getElementById('flowEditor').style.height=box.height+'px';
                // document.getElementById('download-canvas').style.width=box.width+'px';
                // document.getElementById('download-canvas').style.height=box.height+'px';
                // editor=new flowEditor('download-canvas');
                // editor.init(JSON.parse(JSON.stringify(this.editor.getData())),true);
                // editor.attr({
                //   position:[-parseInt(box.x),-parseInt(box.y)]
                // });
                // this.editor.position[0] = -parseInt(box.x);
                // this.editor.position[1] = -parseInt(box.y);
              // editor.position[0] = [-parseInt(box.x)];
              //  editor.position[1] = [-parseInt(box.y)];
               try{
                  this.editor.zr.painter.getRenderedCanvas({
                     backgroundColor:"white"
                  }).toBlob((blob) => {
                    var url = window.URL.createObjectURL(blob);
                    window.console.log(url);
                    window.open(url);
                  }, 'image/png');
               }catch(e){
                   document.getElementById('download-win').style.display='block';
               }
             break;
          //  case 'jpg':
          //       box=this.editor.getBoundingRect(this.editor.nodes.concat(this.editor.groups).concat(this.editor.edges));
          //       document.getElementById('download-canvas').style.width=box.width+'px';
          //       document.getElementById('download-canvas').style.height=box.height+'px';
          //       editor=new flowEditor('download-canvas');
          //       editor.init(JSON.parse(JSON.stringify(this.editor.getData())),true);
          //       editor.attr({
          //         position:[-box.x,-box.y]
          //       });

          //       try{
          //           editor.zr.painter.getRenderedCanvas({
          //               backgroundColor:"#fff"
          //           }).toBlob((blob) => {
          //               var url = window.URL.createObjectURL(blob);
          //               window.open(url);
          //           }, 'image/jpeg');
          //        }catch(e){
          //            document.getElementById('download-win').style.display='block';
          //        }
          //    break;
         }
         this.removeDowloadMsg();
     },


      downloadFile(content, filename) {
          var a = document.createElement('a')
          var blob = new Blob([content])
          var url = window.URL.createObjectURL(blob)
          a.href = url
          a.download = filename
          a.click()
          window.URL.revokeObjectURL(url)
      },

     updateText(){
       var txt=getPlainTxt(this.text);
       this.plainTxt=this.trim(txt);
       
       if(this.editStatus=='editNode'){
         eventBus.$emit('changeNode',{style:{text:this.trim(txt)},nodes:[this.node]});
       }

       if(this.editStatus=='editGroup'){
         eventBus.$emit('changeGroup',{mark:'head',style:{text:this.trim(txt)},group:this.editGroup});
       }

       if(this.editStatus=='editEdge'){
         eventBus.$emit('changeEdge',{mark:'text',style:{text:this.trim(txt)},edge:this.edge});
       }

       this.editModel=false;
       this.editStatus='';
       this.text='';
       this.plainTxt='';
     },
     domText(txt){
       var t=[];
       var txtArr=txt.split('\n');
       txtArr.forEach(tx=>{
          t.push('<div>'+tx+'</div>')
       });

     // window.console.log(t.join(''),+new Date());
       return t.join('');
     },
     closeDownLoadWin(){
        document.getElementById('download-win').style.display='none';
     }
  }
}
</script>

<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.editor{
   position: absolute;
   left:160px;
   top:40px;
   right:0px;
   bottom:0px;
  /* background: url('../assets/image/wg.png') repeat; */
}
#flowEditor{
  width:100%;
  height:100%;
}
.text-editor{
  position: absolute;
  z-index: 300;
  background: #fff;
  border:1px solid #ccc;
  outline: none;
  min-width: 60px;
  font-size:14px;
  line-height: 20px;
}

.download-win{
  box-shadow: 0 0 3px #ccc;
  position: absolute;
  left:50%;
  top:50%;
  transform: translate(-50%,-50%);
  display: none;
  background: #fff;
}
.download-header{
  height:30px;
  background: #f5f5f5;
  line-height: 30px;
  text-indent: 6px;
  font-size: 12px;
}
.download-header span{
  cursor: pointer;
}
.upload-win-content{
  height: calc(100% - 30px);
  overflow-x:hidden;
  overflow-y:auto;
  padding: 0 10px;
  font-size: 12px;
}
.upload-win-footer{
   position: absolute;
   left:0;
   height:40px;
   right:0;
   bottom:0;
   text-align: right;
}

</style>
